@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.5.2/animate.min.css);
@font-face {
  font-family: "Latinka SemiBold";
  font-weight: "bold";
  src: url(/static/media/Latinka-SemiBold.50b703d1.ttf); }

@font-face {
  font-family: "Latinka";
  font-weight: "bold";
  src: url(/static/media/Latinka-Regular.4ca1a812.ttf); }

body {
  margin: 0;
  font-family: "Latinka", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

h1 {
  font-size: 48px;
  margin-top: 0px;
  margin-bottom: 0px; }

h2 {
  font-size: 40px;
  margin-top: 0px;
  margin-bottom: 0px; }

h3 {
  font-size: 36px;
  margin-top: 0px;
  margin-bottom: 0px; }

.adz-blue {
  color: #2096BA; }

.display-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 100px 50px;
  text-align: justify; }
  @media (max-width: 767px) {
    .display-center {
      padding: 100px 10px; }
      .display-center h1 {
        font-size: 24px; }
      .display-center p {
        font-size: 16px; } }

.center-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  padding-top: 100px !important;
  padding-bottom: 200px !important; }
  @media (max-width: 767px) {
    .center-container {
      padding-top: 50px !important;
      padding-bottom: 100px !important; } }
  .center-container .content {
    z-index: 999;
    text-align: center;
    color: #FFF;
    width: 75%; }
    .center-container .content span {
      font-size: 1.5rem;
      font-family: 'Latinka';
      display: block; }
    .center-container .content label {
      font-size: 5rem;
      font-family: 'Latinka SemiBold';
      display: block; }
    @media (max-width: 767px) {
      .center-container .content span {
        font-size: 1rem; }
      .center-container .content label {
        font-size: 2.5rem; } }

.MuiContainer-root {
  z-index: 1; }

.fade-enter {
  opacity: 0.01;
  background: #ef5f00 !important; }

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in;
  background: #ef5f00 !important; }

.fade-exit {
  opacity: 1;
  background: #ef5f00 !important; }

.fade-exit.fade-exit-active {
  opacity: 0.01;
  background: #ef5f00 !important;
  -webkit-transition: opacity 200ms ease-in;
  transition: opacity 200ms ease-in; }

.header-component {
  position: fixed;
  z-index: 999;
  top: 0px;
  width: 100%;
  padding: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box; }
  @media (max-width: 767px) {
    .header-component {
      padding: 10px; } }

a {
  text-decoration: none !important;
  color: #000; }

.home-component {
  font-family: 'Latinka';
  height: 100vh;
  color: #FFF; }
  .home-component .section-work {
    background: #DF6E21;
    display: flex;
    flex-direction: column;
    justify-content: center; }
    .home-component .section-work .work-svg {
      max-height: 500px;
      width: auto; }
    .home-component .section-work p.title {
      font-size: 24px;
      margin-bottom: 20px;
      position: relative;
      letter-spacing: 2px;
      margin-top: 0px; }
      .home-component .section-work p.title::before {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 30px;
        height: 3px;
        background: #fff; }
  .home-component .section-vision {
    height: 50vh;
    background: #2096BA;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative; }
    .home-component .section-vision .vision-svg {
      max-width: 350px;
      height: auto;
      margin-top: -80px; }
    .home-component .section-vision .contact-svg {
      max-width: 650px;
      height: auto;
      margin-top: -80px; }
    .home-component .section-vision p.title {
      font-size: 20px;
      margin-bottom: 20px;
      position: relative;
      letter-spacing: 2px; }
      .home-component .section-vision p.title::before {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 30px;
        height: 3px;
        background: #fff; }
    .home-component .section-vision h3 {
      font-size: 28px; }
  .home-component .content-container {
    position: absolute;
    bottom: 20px; }
  .home-component .section-team {
    background: #252b61; }


.works-component {
  background: #ef5f00;
  font-family: 'Latinka';
  padding: 100px 0px; }
  .works-component .block {
    text-align: left;
    justify-content: flex-start;
    position: relative;
    padding-bottom: 250px; }
    @media (max-width: 767px) {
      .works-component .block {
        padding-bottom: 50px; } }
  .works-component .video-container {
    max-width: 900px; }
    .works-component .video-container video {
      width: 100%; }
  .works-component .content-container {
    height: 350px;
    width: 300px;
    background: #FFF;
    border-left: 15px solid #ef5f00;
    border-bottom: 15px solid #ef5f00;
    border-top: 15px solid #ef5f00;
    border-color: #ef5f00;
    padding: 40px 50px;
    position: absolute;
    right: 5%;
    bottom: 0px; }
    @media (max-width: 767px) {
      .works-component .content-container {
        height: 100%;
        padding: 20px; } }
    @media (max-width: 600px) {
      .works-component .content-container {
        box-sizing: border-box;
        position: relative;
        width: 100%;
        border: unset;
        left: 0px; } }
    .works-component .content-container label {
      font-size: 38px;
      font-weight: bold;
      font-family: 'Latinka SemiBold'; }
    .works-component .content-container p {
      color: #000;
      font-size: 16px; }

.about-component {
  background: #2096BA;
  padding-top: 150px;
  padding-bottom: 150px;
  height: 100%;
  color: #FFF; }
  @media (max-width: 767px) {
    .about-component {
      padding-top: 50px;
      padding-bottom: 20px; } }
  .about-component .block-2 p {
    text-align: left; }
  .about-component .block-2 .vision-container {
    justify-content: flex-start;
    background: #ef5f00;
    border: 5px solid rgba(0, 0, 0, 0);
    height: 100%;
    border-radius: 20px;
    padding: 20px; }

.contact-component {
  background: -webkit-gradient(linear, left top, left bottom, from(#2096BA), to(#252b61));
  background: linear-gradient(#2096BA, #252b61);
  padding-top: 150px;
  padding-bottom: 150px;
  height: 100vh;
  color: #FFF;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  .contact-component a {
    color: #FFF;
    display: block; }
    .contact-component a:hover {
      color: rgba(37, 43, 97, 0.8); }
    @media (max-width: 767px) {
      .contact-component a h1 {
        font-size: 32px; } }

